import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers, RequestMethod } from '@angular/http';
import { map } from "rxjs/operators";
import { Project } from './project.model';
import { ConnectionService } from './connection.service';

@Injectable({
  providedIn: 'root'
})
export class ProjectService {
  
  
  selectedProject : Project;
  projectsList : Project[];
  constructor(private http: Http, private connectionService: ConnectionService ) { }
  readonly rootUrl = this.connectionService.rootUrl;
  postProject(prj : Project){
    var body = JSON.stringify(prj);  
    var headerOptions = new Headers({ 'Content-Type': 'application/json' });
    headerOptions.append('Authorization', `Bearer ${localStorage.getItem('userToken')}`);
    var requestOptions = new RequestOptions({ method: RequestMethod.Post, headers: headerOptions });
    return this.http.post(this.rootUrl + '/api/CreateProject', body, requestOptions).pipe(map(x => x.json()));
  }  
 
  putProject(id, prj) {    
    var body = JSON.stringify(prj);
    var headerOptions = new Headers({ 'Content-Type': 'application/json', "Authorization" : "Bearer " + localStorage.getItem('userToken') });
    var requestOptions = new RequestOptions({ method: RequestMethod.Put, headers: headerOptions });
    return this.http.put(this.rootUrl + '/api/UpdateProject/' + id, body, requestOptions).pipe(map(res => res.json()));
  }

  getAllProjects(){
    this.http.get(this.rootUrl + '/api/GetAllProjects')
    .pipe(map((data : Response) => {
      return data.json() as Project[];
    })).toPromise().then(x => {
      this.projectsList = x ;      
    })
  }  

  getAllProjectsByTag(id){
    this.http.get(this.rootUrl + '/api/GetProjectsByTag/' + id)
    .pipe(map((data : Response) => {
      return data.json() as Project[];
    })).toPromise().then(x => {
      this.projectsList = x ;      
    })
  }  

  returnAllProjects(){
    return this.http.get(this.rootUrl + '/api/GetAllProjects')
    .pipe(map(res => res.json() as Project[]))
  }  
  
  deleteProject(id: number) {
    var headerOptions = new Headers({ 'Content-Type': 'application/json', "Authorization" : "Bearer " + localStorage.getItem('userToken') });    
    //var header = new HttpHeaders({"Authorization" : "Bearer " + localStorage.getItem('userToken')});
    var requestOptions = new RequestOptions({ method: RequestMethod.Delete, headers: headerOptions });
    return this.http.delete(this.rootUrl + '/api/DeleteProject/' + id, requestOptions).pipe(map(res => res.json()));
  }



}
